@import "../setup";

.bermudez{
    font-family: $bebas;
    font-size: 40px;
    color: white;
    margin: 0px;
    padding: 0px;
    grid-area: bermudez;
    max-height: 40px;
}

.web{
    font-family: $bebas;
    font-size: 30px;
    color: $darkGreen;
    margin: 0px;
    padding: 0px;
    grid-area: web;
    line-height: 30px;
    text-align: center;
    max-height: 30px;
}

.signs{
    font-family: $bebas;
    font-size: 30px;
    color: $lightGreen;
    margin: 0px;
    padding: 0px;
    max-height: 30px;
    grid-area: signs;
    line-height: 30px;
    text-align: center;
}

.logo{
    width: 132px;
    height: 65px;
    display: grid;
    grid-template-columns: "50% 50%";
    grid-template-rows: "40px 30px";
    grid-template-areas: "bermudez bermudez"
                            "signs web";
}